@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
h1 {
  text-align: center;
  color: rgb(14, 14, 56);
  font-family: Roboto;
  font-weight: 600;
  font-size: 2.5em;
}

h2 {
  text-align: center;
  color: rgb(14, 14, 56);
  font-family: Roboto;
  font-weight: 400;
  font-size: 1.5em;
}

p {
  margin-left: 7rem;
  margin-right: 7rem;
  text-align: center;
  display: flex;
  justify-content: center;
  color: rgb(14, 14, 56);
  font-family: Roboto;
  font-weight: 400;
  font-size: 1em;
}

.game-form {
  width: 100%;
  max-width: 400px;
  margin: 2rem auto;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  color: rgb(14, 14, 56);
}

.game-form__row {
  margin-bottom: 0.75rem;
}

.game-form__label {
  width: 100%;
  display: block;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: left;
}

.game-form__input {
  width: 100%;
  display: block;
  padding: 0.5rem 0.5rem;
  background: #a2e5ff;
  font-size: 1.125rem;
  color: rgb(14, 14, 56);
  font-weight: 600;
  border: 2px solid rgb(14, 14, 56);
  border-radius: 0.2rem;
}

.game-form__submit {
  border: none;
  color: white;
  display: block;
  margin: 2rem auto;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: rgb(14, 14, 56);
  background-image: linear-gradient(to right, #39b54a, #1dbfff);
  border-radius: 4px;
  font-weight: 600;
}

.game-form__submit:hover {
  background-image: none;
  background-color: white;
  border: 3px solid #39b54a;
  color: #1dbfff;
}

.footer {
  margin-top: 2rem;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: rgb(14, 14, 56);
}

.header__logo {
  background-image: url(/static/media/compost-example.f2fa1ea1.png);
  background-color: #cccccc;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header {
  margin: 0 0 2rem 0;
  text-align: center;
}

.header__logo {
  font-size: 2rem;
}

.header__nav {
  font-size: 1.25rem;
  font-family: Roboto;
  padding-top: 2rem;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  font-family: inherit;
}

*:before,
*:after {
  box-sizing: inherit;
}

a {
  transition: color 0.1s, background-color 0.1s;
}

a:link {
  background-color: transparent;
  color: #1dbfff;
  text-decoration: none;
}

a:visited {
  background-color: transparent;
  color: rgb(14, 14, 56);
}

a:focus {
  background-color: #1685b1;
  color: white;
}

a:hover {
  background-color: #1dbfff;
  color: white;
}

